Data, { token: this.token }))\n        if (res.data.code == 1) {\n          this.init()\n          this.$message({ message: res.data.msg, type: 'success' })\n        } else {\n          this.$message.error(res.data.msg)\n        }\n      } catch (err) {\n        throw (err)\n      }\n    },\n\n\n    // 权限列表,当前所在权限页  \n    erAuthList () {\n      var id = this.$route.query.id;\n      this.curId = id;\n      this.$axios.post('admin/Auth/erAuth', {\n        token: this.token,\n        id: id\n      })\n        .then(res => {\n          if (res.data.code == 1) {\n            var arr = res.data.data;\n            for (var i = 0; i < arr.length; i++) {\n              if (arr[i].son) {\n                if (arr[i].name == '服务车系统设置') {\n                  this.seCurId = arr[i].id;\n                  this.threeAuthList = arr[i].son;\n                }\n              }\n            }\n            this.authList = arr;\n          } else {\n            this.$alert(res.data.msg, '提示', {\n              type: 'error'\n            });\n          }\n        })\n        .catch(err => {\n          alert(err);\n        })\n    }\n  },\n  updated () {\n\n  },\n  mounted () {\n    this.init() //初始化数据  \n    this.erAuthList()\n\n  },\n}\n</script>\n<style scoped>\n>>> .el-dialog__body {\n  padding-top: 30px !important;\n}\n.quote:after {\n  display: block;\n  clear: both;\n  content: \"\";\n}\n.rewardImg {\n  width: 70px;\n  height: 70px;\n}\n\n.add_goods {\n  float: right;\n  margin-top: 25px;\n  margin-right: 20px;\n}\n.ellipsis {\n  width: 100px;\n  text-overflow: ellipsis;\n  white-space: nowrap;\n  overflow: hidden;\n}\n>>> .el-dialog__header {\n  text-align: center;\n}\n>>> .w-e-toolbar {\n  overflow: auto;\n}\n</style>"],"sourceRoot":""}]);

